@import url(https://fonts.googleapis.com/css?family=Exo+2);
@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: 'Againts';
  src: url(../../static/media/againts.08c004a0.eot) format('eot'), url(../../static/media/againts.c01d8d3b.woff) format('woff'), url(../../static/media/againts.7c801b5d.ttf) format('truetype'), url(../../static/media/againts.b70d4fe7.svg#Againts) format('svg');
}

html,
body {
  margin: 0;
  height: 100%;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background: #454d5d 50%;
  background-size: cover;
  text-align: center;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.logo,
.title,
.slogan,
.navigation {
  opacity: 0;
  -webkit-animation: fadeIn 1s ease forwards;
          animation: fadeIn 1s ease forwards;
}

.logo {
  margin: 0 auto 4rem;
  width: 12.5rem;
  height: 12.5rem;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.title,
.slogan {
  font-family: Againts;
}

.title {
  margin: 0 0 1rem;
  color: #fff;
  text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.8);
  font-size: 5rem;
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}

.slogan {
  margin: 0 0 4rem;
  color: #eee;
  text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.6);
  font-size: 2rem;
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-animation-delay: 0.55s;
          animation-delay: 0.55s;
}

.navigation li + li::before {
  margin: 0.5rem;
  color: #aaa;
  content: '\B7';
}

.navigation a {
  color: #99e9f2;
  text-decoration: none;
  font-family: 'Exo 2', sans-serif;
  -webkit-transition: color 0.5s, text-shadow 0.5s;
  transition: color 0.5s, text-shadow 0.5s;
}

.navigation a:hover {
  color: #22b8cf;
  text-shadow: 0 0 0.5rem rgba(255, 255, 255, 0.5);
}

.navigation a:focus {
  outline: 0;
  color: #3bc9db;
  text-shadow: 0 0 0.5rem rgba(255, 255, 255, 0.5);
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-filter: blur(1rem);
          filter: blur(1rem);
  -webkit-transition: all 2.5s;
  transition: all 2.5s;
  -webkit-transform: scale(1.125);
          transform: scale(1.125);

  object-fit: cover;
}

.background.loaded {
  opacity: 1;
  -webkit-filter: none;
          filter: none;
  -webkit-transform: none;
          transform: none;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(2rem) scale(0.8);
            transform: translateY(2rem) scale(0.8);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(2rem) scale(0.8);
            transform: translateY(2rem) scale(0.8);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

